import Vue from 'vue';
import Router from 'vue-router';
import ComLogin from '@/components/page/ComLogin';   //登录页
import WithdrawManage from '@/components/page/WithdrawManage'; //提现管理
import UserIdCardManage from '@/components/page/UserIdCardManage'; //用户实名认证管理
import orderManage from '@/components/page/orderManage'; //vip订单管理
import UserListManage from '@/components/page/UserListManage'; //用户列表
import platformManage from '@/components/page/platformManage'; //资金账户列表
import platformLogs from '@/components/page/platformLogs'; //变动日志

Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/ComLogin',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/Main/WithdrawManage',
      name: 'WithdrawManage',
      component: WithdrawManage
    },{
      path: '/Main/UserIdCardManage',
      name: 'UserIdCardManage',
      component: UserIdCardManage
    },{
      path: '/Main/orderManage',
      name: 'orderManage',
      component: orderManage
    },{
      path: '/Main/UserListManage',
      name: 'UserListManage',
      component: UserListManage
    },{
      path: '/Main/platformManage',
      name: 'platformManage',
      component: platformManage
    },{
      path: '/Main/platformLogs',
      name: 'platformLogs',
      component: platformLogs
    },
  ]
});



